import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, NavigationExtras, Router } from '@angular/router';
import { Location } from '@angular/common';
import { ChannelService } from 'src/app/services/channel.service';
import { CartItem, Order, OrderStatus } from 'src/app/objects/cart.model';
import { Additional, OrderItem } from 'src/app/objects/food-drink-menu.model';
import { OrderService } from 'src/app/services/order.service'; 
import { AngularFirestoreDocument, DocumentData } from '@angular/fire/firestore';
import { LoadingController } from '@ionic/angular';

@Component({
  selector: 'app-orders',
  templateUrl: './orders.page.html',
  styleUrls: ['./orders.page.scss'],
})
export class OrdersPage implements OnInit {

  cid: string;
  orders:Order[];
  orderStatus: OrderStatus;
  loading: HTMLIonLoadingElement;
  filter_status:string = 'hepsi';
  filter_start_date:Date;
  filter_end_date:string;
  showFilterCard: Boolean = false; 
  startAfterDoc: DocumentData;
  limit:number=1;

  constructor(public route: ActivatedRoute,
              private orderService:OrderService,
              private loadingController : LoadingController,
              public router: Router, 
              private location:Location){
                this.orderStatus        = new OrderStatus();
                this.filter_end_date    = (new Date()).toString();
                this.filter_start_date  = new Date(0); 
              }

  ngOnInit() {
    this.cid   = this.route.snapshot.paramMap.get('cid')
    this.getOrders()
  }

  async getOrders(){
    this.loading = await this.loadingController.create({ 
      message: 'Please wait...',
      duration: 5000
    })  
    await this.loading.present();
    
    this.startAfterDoc  = null
    let filter_end_date = new Date(this.filter_end_date)
    this.orderService.getOrders(this.cid,this.filter_status,this.filter_start_date,filter_end_date,this.startAfterDoc,this.limit).snapshotChanges().subscribe(async (orders)=>{
      await this.loading.dismiss();
      this.orders = orders.map(order=>{
        return {id:order.payload.doc.id,...order.payload.doc.data()}
      }) 
      if(orders.length){
        this.startAfterDoc = orders[orders.length-1].payload.doc;
      }
    })
  }

  async getNextOrders(){
    let filter_end_date = new Date(this.filter_end_date)
    let filter_start_date = new Date(this.filter_start_date)
    let reference = this.orderService.orderReference(this.cid)
                                    .orderBy('created_at','desc')
                                    .where('created_at','>',filter_start_date)
                                    .where('created_at','<',filter_end_date)
                                    .startAfter( this.startAfterDoc)
                                    .limit(this.limit);
                                    
    this.loading = await this.loadingController.create({ 
      message: 'Please wait...',
      duration: 5000
    })  
    await this.loading.present();

    if(this.filter_status === undefined || this.filter_status === 'hepsi'){
      reference.get().then(async (orders)=>{ 
                  await this.loading.dismiss();
                  orders.docs.map(order=>{ 
                    this.orders.push(<Order>{id:order.id,...order.data()})
                  })
                  if(orders.docs.length<=this.limit){
                    this.startAfterDoc = orders.docs[orders.docs.length-1];
                  }else{
                    this.startAfterDoc = null
                  }
              })
    }else{
      reference.where('status.status','==',this.filter_status).get().then(async (orders)=>{ 
                  await this.loading.dismiss();
                  orders.docs.map(order=>{
                    this.orders.push(<Order>{id:order.id,...order.data()})
                  })
                  if(orders.docs.length<=this.limit){
                    this.startAfterDoc = orders.docs[orders.docs.length-1];
                  }else{
                    this.startAfterDoc = null
                  }
              })
    }
  }

  next(){ 
    this.getNextOrders();  
  }

  back(){
    this.location.back();
  }

  getOrderPrice(order:Order){  
    let total:number = 0;
    order.cart_items.map((cart_item:CartItem)=>{
      cart_item.food.orders.map((order:OrderItem)=>{
        total += order.additional.quantity * order.additional.price
      })
      total   += cart_item.food.price;
    })
    return total;
  }

  showOrderDetails(order:Order){
    let navigationExtras: NavigationExtras = {
      state: { order: order, cid: this.cid }
    };
    this.router.navigate(['/order-details'],navigationExtras) 
  }

  switchFilterCard(){
    this.showFilterCard = !this.showFilterCard
  }

  getStatus(order:Order){
    return this.orderStatus.getValue(order.status.status)
  }
}
